const Types = {
    SETROOM: 'setRoom',
    UPDATE_STAGE: 'updateStage',
    LEAVE: 'LEAVE',
    ENDTHECLASS: 'ENDTHECLASS'
}

export default {
    namespaced: true,
    state: {
        roomId: 1,
        startTime: 1,
        endTime: 1,
    },
    mutations: {
        [Types.SETROOM](state, result) {
            state.roomId = result.room_id;
            state.startTime = result.start_time;
            state.endTime = result.end_time;
        },
        [Types.UPDATE_STAGE](state, index) {
            state.stageIndex = index;
        }
    },
    actions: {
        setRoom({ commit }, data) {
            commit(Types.SETROOM, data)
        },
        updateStage({ commit }, index) {
            commit(Types.UPDATE_STAGE, index);
        }
    },
    getters: {

    }
}