import BaseApiService from '@/service/BaseApiService'

class Api extends BaseApiService {
    constructor() {
        super();
        // super.prefix = window.location.origin + "/";
    }

    /**
     * 课程结束检查回放视频
     */
    getPlayBackInfo = (id) => this.request('live_playback', { room_id: id });
    
    /**
     * 检查课程信息
     */
    getClassInfo = (id, name, type) => this.request('live_monitor', { room_id: id, name: name, type:type});

    /**心跳 */
    testHeart = () => this.request('heart');


}

export default {
    install(Vue) {
        Vue.prototype.Api = new Api();
        Vue.prototype.$api = Vue.prototype.Api;
    }
}