<template>
  <div class="fill">
    <div id="player" class="fill player"></div>
    <div class="fill poster" v-show="show">
      <img class="fill" v-if="videodata && videodata.poster" :src="videodata.poster">
    </div>

    <div class="fill bg" v-if="!show && !playing" @click="playerHander">
      <img class="fill" v-if="videodata && videodata.poster" :src="videodata.poster">
      <div class="fill mask"></div>
      <img class="playbtn" :src="playBtn">
    </div>

    <BaseWindow v-if="errorTips" class="alert" label="刷新" @change="refreshClick">检测到当前网络较差，请刷新重试！</BaseWindow>
  </div>
</template>

<script>
import Player from "xgplayer"
import HlsJsPlayer from 'xgplayer-hls.js'

import playBtn from './../assets/image/poster_play.png';
import load_gif from '@/assets/image/loading.gif';

import BaseWindow from "./BaseWindow.vue";
import { reload } from '@/utils/common.js';

// export
export default {
  components: {
    BaseWindow
  },
  props: {
    videodata: {
      type: Object,
      default: () => { }
    }
  },
  data() {
    return {
      load_gif,
      playBtn,
      player: null,
      playing: false,
      loading: false,
      show: false,
      errorTips: null,
    }
  },
  computed: {
    videoshow() {
      return this.isWeixinBrowser() && this.playing;
    }
  },
  methods: {
    initVideo() {
      var options = {
        id: 'player',
        url: this.videodata.src,
        fluid: true,//流式布局
        videoInit: true,//初始化首帧
        poster: this.videodata.poster,
        playsinline: true,
        width: document.documentElement.clientWidth,
        height: document.documentElement.clientHeight,
        autoplay: true,
        volume: 1,
        'x5-video-player-type': 'h5',
      };

      console.log('play', this.videodata.src);
      if (this.videodata && this.videodata.src.indexOf('.m3u8') != -1) {
        Object.assign(options, {
          ignores: ['progress', 'time'],
          closeVideoClick: true,
          closeVideoDblclick: true,
          closeVideoTouch: true,
          closePlayerBlur: true,
          closeControlsBlur: true,
          closeFocusVideoFocus: true,
          closePlayVideoFocus: true
        });
        this.player = new HlsJsPlayer(options);
        const that = this;
        setInterval(() => {
          that.loading = that.player.inWaitingStart && that.player.inWaitingStart > 0;
        }, 100);

        setInterval(() => {
          that.errorTips = that.player.inWaitingStart;
        }, 10000);
      } else {
        this.player = new Player(options);
      }
      this.player.once('ready', this.playerReady);
      this.player.once('complete', this.playerComplete);
      this.player.once('destory', this.playerDestory);
      this.player.on('error', this.playerError);

      this.player.on('play', this.playerPlay);
      this.player.on('playing', this.playerPlay);

      this.player.on('pause', this.playerPause);
      this.player.on('ended', this.playerPause);
      window.player = this.player;
    },
    playerReady() {
      console.log('xgplay --> playerReady');
    },

    playerComplete() {
      console.log('xgplay --> playerComplete');
      document.querySelector('#player').removeAttribute('style');
      this.removeChild('.xgplayer-start');
      this.removeChild('.xgplayer-replay');
      var list = document.querySelectorAll('xg-tips');
      list.forEach(node => {
        node.parentNode.removeChild(node);
      });

      document.querySelector('.xgplayer-loading').innerHTML = "<img src='" + this.load_gif + "' alt='loading'>";
    },

    playerDestory() {
      console.log('xgplay --> playerDestory');
    },
    playerPlay() {
      this.playing = true;
    },
    playerPause() {
      this.playing = false;
    },
    playerHander() {
      this.player && this.player.play();
    },
    playerError(event) {
      console.log('ERROR ->', event);
    },
    refreshClick() {
      reload();
    },
    removeChild(el) {
      var node = document.querySelector(el);
      node && node.parentNode.removeChild(node);
    }
  },
  mounted() {
    if (this.videodata && this.videodata.src) {
      this.initVideo();
      this.show = false;
    } else {
      this.show = true;
    }
  }
}
</script>
