<template>
  <div class="wrapper">
    <!-- :style="{width:page.width+'px', height:page.height+'px', zoom:page.ratio}" -->
    <VideoPlayer v-if="videoData" :videodata="videoData"></VideoPlayer>

    <BaseWindow
      v-if="playerror"
      class="alert"
      label="刷新"
      @change="refreshClick"
    >当前课程数据获取异常，请反馈到我们，我们来尽快帮您处理，谢谢您的谅解 拨打400-163-6161</BaseWindow>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import BaseWindow from "./views/BaseWindow.vue";
import VideoPlayer from "./views/VideoPlayer.vue";
import { queryGet, reload } from '@/utils/common.js';
export default {
  name: "app",
  components: {
    BaseWindow,
    VideoPlayer
  },
  data() {
    return {
      checked: false,
      loading: false,
      classId: null,
      title: "",
      roomData: null,
      videoData: null,
      heartTimeId: 0,
      playerror: false
    };
  },
  computed: {
    ...mapState("page", {
      page: state => state
    })
  },
  methods: {
    ...mapActions("page", ["onResize"]),
    init(data) {
      var _hmt  =  window._hmt || [];
      var hm = document.createElement("script");
      hm.src = "https://hm.baidu.com/hm.js?bf270b9861f2d514b0984524dcbccc50";
      document.querySelector('head').appendChild(hm);

      this.roomData = data;
      console.log('room data', data);
      this.getVideoInfo();
    },
    getVideoInfo() {
      const that = this;
      //获取回放地址
      this.$api.getPlayBackInfo(that.roomData.id).then(({ isSuccess, result: { url, title, image } }) => {
        if (isSuccess) {
          // url = "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm";
          // url = "https://resource.uuabc.com/video/mp4/3.mp4"
          this.title = title || "FasTrack English";
          document.title = this.title;
          if (url) {
            that.videoData = { type: 'video/mp4', src: url, poster: image };
          } else {
            that.getRoomMetaData();
          }
        } else {
          that.getRoomMetaData();
        }
      }).catch(err => {
        this.playerror = true;
      });
    },
    getRoomMetaData() {
      const that = this;
      this.$api.getClassInfo(
        this.roomData.id, this.roomData.name, this.roomData.type
      ).then(({ isSuccess, result: { image, url, stream } }) => {
        if (isSuccess) {
          // image = "https://surmon-china.github.io/vue-quill-editor/static/images/surmon-1.jpg";
          // url = 'https://logos-channel.scaleengine.net/logos-channel/live/biblescreen-ad-free/playlist.m3u8';
          that.videoData = {
            withCredentials: false,
            type: "application/x-mpegURL",
            src: stream == 1 ? url : null,
            poster: image
          };
        }
      }).catch(err => {
        this.playerror = true;
      });
    },
    refreshClick() {
      reload();
    }
  },
  mounted() {
    this.onResize(document.documentElement);
    window.onresize = () => {
      this.onResize(document.documentElement);
    };

    //查找课程ID
    function queryPathName(path) {
      const reg = new RegExp("\\/live\\/[a-zA-Z0-9-_]+", "gi");
      const result = reg.exec(window.location.href);
      if (result && result[0]) {
        return result[0].replace(path, '');
      } else {
        return null;
      }
    }

    this.checked = true;
    this.init({
      name: queryPathName('/live/'),
      type: queryGet('type'),
      id: queryGet('roomid')
    });
  }
};
</script>

